*{margin:0; padding:0;}
li{list-style-type:none;}
a:link, a:visited{text-decoration:none; color:#000;}

body{
    background-color:#eee;
}

.nav{
    padding-left:15px;
    width:100%;
    height:90px;
    
}
.nav h1{
    width:100%;
    height:100%;
    font-size:20px;
}

.nav a img{
    width:30px;
    height:30px;
    vertical-align:middle;
    padding-right:5px;
}
.nav button{
    position:absolute;
    top:10px; right:15px; 
    width:100px;
    height:30px; 
    border:none; 
    border-radius:10px;
    background-color: #333; 
    color:white;
    
}

.connection{
    margin:0 auto;
    text-align:center;
    border-radius: 10px;
    border:1px solid #333;
    width:160px;
    height:20px;
    font-size:14px;
    color:#333;
}

.swap{
    margin:10px auto 0;
    padding:5px;
    width:200px;
    height:130px;
    border:none;
    border-radius: 10px;
    box-shadow: 0px 0px 7px #ddd;
    background-color: #fff;
}

.swap .swapIcon{
    padding-left:5px;
    width: 15px;
    height:15px;
    vertical-align: middle;
}

.swap h2{
    font-size:13px;
    padding:5px;
}

.swap select{
    width:80px;
    height:30px;
    margin-left:10px;
    border:none;
    font-size:16px;
    font-weight:700;
}

.swap .amount{
    position:relative;
    padding:3px 0 5px;
}
.swap span {
    color:#555;
    font-size:12px;
}

.swap button{
    position:absolute; bottom:5px; right:5px;
    border:none;
    background-color: #fff;
    font-weight:500;
    font-size:13px;
}

.swap input{
    margin-top:10px;
    width:100%;
    border:none;
    border-bottom: 1px solid #ccc;
    cursor:pointer;
}

.footer{
    padding:3px 10px;
    position:fixed;
    bottom:0;
    display:flex;
    width:100%;
    height:50px;
    line-height: 50px;
}

.footer .info span{
    font-size:12px;
    padding-right:10px;
}

.footer .info span img{
    padding-right:3px;
    vertical-align: middle;
    width:20px;
    height:20px;
}
.footer ul{
    display:flex;
    position:absolute;
    right:150px;
}

.footer ul li img{
    vertical-align: middle;
    padding-right:5px;
}

.footer select{
    position:absolute;
    right:2%;
    top:20px;
}


